<template>
  <div class="errorCode">
    <toast :describe="errorMsg" v-if="ToastType==='publicToast'" position="bottom"></toast>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import toast from '../../components/common/toast'
export default {
  name: 'errorCode',
  components: {
    toast
  },
  data () {
    return {}
  },
  computed: {
    ...mapState(['ToastType']),
    ...mapGetters(['errorMsg'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.errorCode{}
</style>



