<template>
  <div class="password">
    <head-top :title="$t('userPage.changePassword')" goBack="true"></head-top>
    <div class="password_container">
      <div class="column">
        <span>{{$t('userPage.originalPassword')}}</span>
        <em>
          <div class="top-pixel"></div>
          <input type="text" v-model="oldPsd" :placeholder="$t('userPage.enterPassword')">
          <div class="bom-pixel"></div>
        </em>
      </div>
      <div class="column">
        <span>{{$t('userPage.newPassword')}}</span>
        <em>
          <div class="top-pixel"></div>
          <input type="text" v-model="newPsd" :placeholder="$t('userPage.newlPassword')">
          <div class="bom-pixel"></div>
        </em>
        <em>
          <input type="text" v-model="newPsdAgain" :placeholder="$t('userPage.newlPasswordAgain')">
          <div class="bom-pixel"></div>
        </em>
      </div>
      <div class="themeButton finish" @click="complete">
        <span v-if="!running">{{$t('userPage.finished')}}</span>
        <span v-if="running">
          <img src="../../image/adddirver/Loadingcirclesfreeicon.png">{{$t('userPage.revising')}}
        </span>
      </div>
    </div>

    <!-- 吐司 -->
    <toast :describe="$t('userPage.enterPassword')" v-if="ToastType==='inputOldPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.samePasswordError')" v-if="ToastType==='sameNewPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.passwordInconsistentError')" v-if="ToastType==='newPsdDifferError'" position="bottom"></toast>
    <toast :describe="$t('userPage.newlPassword')" v-if="ToastType==='limitPsdError'" position="bottom"></toast>

    <toast :describe="$t('userPage.originalPasswordError')" v-if="ToastType==='wrongPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.failedChangePassword')" v-if="ToastType==='changePsdFailedError'" position="bottom"></toast>
    <toast :describe="$t('userPage.successfullyChanged')" v-if="ToastType==='changePsdSuccessError'" position="bottom"></toast>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import headTop from '../../components/header/header'
import toast from '../../components/common/toast'
import { passwordValidate } from '../../config/utils.js'
import {
  changePasswordRequest
} from '../../service/method.js'
import {
  changePasswordResponse
} from '../../service/getData.js'

export default {
  data () {
    return {
      oldPsd: '',
      newPsd: '',
      newPsdAgain: '',
      running: false // 正在修改密码
    }
  },

  mounted () {
    // 修改密码返回
    changePasswordResponse(this.NAVIGATOR, this)
  },

  components: {
    headTop,
    toast
  },

  computed: {
    ...mapState([
      'ToastType'
    ]),

    ...mapGetters(['errorCode'])
  },

  methods: {
    complete () {
      if (this.oldPsd === '') {
        this.$store.state.ToastType = 'inputOldPsdError'
      } else if (!passwordValidate(this.newPsd) || !passwordValidate(this.newPsdAgain)) {
        this.$store.state.ToastType = 'limitPsdError'
      } else if (this.newPsd === this.oldPsd) {
        this.$store.state.ToastType = 'sameNewPsdError'
      } else if (this.newPsd !== this.newPsdAgain) {
        this.$store.state.ToastType = 'newPsdDifferError'
      } else {
        changePasswordRequest(this.NAVIGATOR, this, this.newPsd, this.oldPsd)
        this.running = true
      }
    }
  },
  watch: {
    errorCode (newValue, oldValue) {
      if (!newValue) {
        this.running = false
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.password{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .password_container{
    .fs-noTop;
    .column{
      .wh(100%, auto);
      margin-top: 17px;
      span{
        .wh-l(100%, 45px);
        display: block;
        padding: 0 15px;
        font-size: 15px;
        text-align: left;
      }
      em{
        .wh-l(100%, 55px);
        display: block;
        padding: 0 15px;
        position: relative;
        input{
          .wh(100%, 100%);
          display: block;
          font-size: 13px;
        }
      }
    }
    .finish{
      margin: 80px auto;
      span{
        font-size: 17px;
        color: #fff;
        vertical-align: middle;
        img{
          height: 17px;
          position: relative;
          top: 1px;
          left: -5px;
          transform-origin: center;
          animation: rotate 1s infinite;
          margin-left: 5px;
        }
      }
    }
  }
}
</style>



