<template>
  <div class="state">
    <div class="state-container" @click="clickHandle">
      <div class="table">
        <div class="conent">
          <div class="view1" v-show="viewSwitch===1">
            <div id="myChart"></div>
            <div class="nav">
              <div class="list">
                <li @click="navHandle(1)">时<b v-show="nav===1"></b></li>
                <li @click="navHandle(2)">日<b v-show="nav===2"></b></li>
                <li @click="navHandle(3)">月<b v-show="nav===3"></b></li>
              </div>
              <div class="time">
                <span v-show="nav===1">最近<b>24</b>小时</span>
                <span v-show="nav===2">最近<b>7</b>天</span>
                <span v-show="nav===3">最近<b>7</b>月</span>
              </div>
            </div>
            <div class="realTime">
              <ul>
                <li>
                  <span>{{realTime.voltage}}</span>
                  <strong>V</strong>
                </li>
                <li>
                  <span>{{realTime.electricity}}</span>
                  <strong>A</strong>
                </li>
                <li>
                  <span>{{realTime.powerValue}}</span>
                  <strong>W</strong>
                </li>
                <li>
                  <span>{{realTime.frequency}}</span>
                  <strong>Hz</strong>
                </li>
                <li>
                  <span>{{realTime.powerFactor}}</span>
                  <strong>Pf</strong>
                </li>
              </ul>
            </div>
          </div>
          <div class="view2" v-show="viewSwitch===2">
            <div class="container">
              <ul class="left">
                <li>
                  Peak Volt
                  <span>{{maxVoltage.toFixed(3)}}</span>
                </li>
                <li>
                  Peak Watts
                  <span>{{MaximumPower.toFixed(3)}}</span>
                </li>
                <li>
                  <img src="../../image/state/wifi/timing@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: countdown.percentage + '%'}"></div>
                  </div>
                  <span>{{countdown.hour}}<b>h</b>{{countdown.minute}}<b>m</b>{{countdown.second}}<b>s</b></span>
                </li>
                <li>
                  <img src="../../image/state/wifi/fixedtile@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: progressValue.powerConsumption}"></div>
                  </div>
                  <span>{{eAlarmValue - electricityValue}}<b>W</b></span>
                </li>
                <li>
                  <img src="../../image/state/wifi/fixedfee@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: progressValue.spend}"></div>
                  </div>
                  <span>{{sAlarmValue - spendValue}}<b>$</b></span>
                </li>
                <li>
                  <div class="load">OVERLOAD</div>
                  <span>{{overload}}<b></b></span>
                </li>
              </ul>
              <ul class="right">
                <li>Running time</li>
                <li>
                  {{running.hour}}<b>h</b>
                  {{running.minute}}<b>m</b>
                  {{running.second}}<b>s</b>
                </li>
                <li>
                  <div class="box">
                    <span>{{rate}}</span>
                    <strong>RATE</strong>
                  </div>
                  <div class="num">
                    {{ratePrice}}<span>$</span>
                  </div>
                </li>
                <li>
                  <div class="box">GHG</div>
                  <div class="num">
                    {{GHG}}<span>kg</span>
                  </div>
                </li>
                <li>
                  <div class="box">
                    <div class="top">
                      <span>{{nowTime.date}}</span>
                      <strong>{{nowTime.week}}</strong>
                    </div>
                    <div class="bottom">
                      <span>
                        <em>AM</em>
                        <b>PM</b>
                      </span>
                      <strong>
                        <em>{{nowTime.time}}</em>
                      </strong>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="view3" v-show="viewSwitch===3">
            <div id="Pillar"></div>
            <div class="nav">
              <ul>
                <li>Day</li>
                <li>Week</li>
                <li>Month</li>
                <li>Year</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="button">
        <ul>
          <li @click="seleteItem(1)">
            <img src="../../image/state/wifi/realtime@3x.png" alt="">
          </li>
          <li @click="seleteItem(2)">
            <img src="../../image/state/wifi/data@3x.png" alt="">
          </li>
          <li @click="seleteItem(3)">
            <img src="../../image/state/wifi/history@3x.png" alt="">
          </li>
          <li @click="seleteItem(4)">
            <img src="../../image/state/wifi/onoff@3x.png" alt="">
            <span>
              <img src="../../image/state/wifi/off@3x.png" v-show="!powerSwitch" alt="">
              <img src="../../image/state/wifi/on@3x.png" v-show="powerSwitch" alt="">
            </span>
          </li>
          <li @click="seleteItem(5)">
            <img src="../../image/state/wifi/config@3x.png" alt="">
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'
import { numToTimeFormat, digitFormat, weekEnglish } from '../../config/utils.js'
import {
  deviceHistoryDataRequest,
  setStatusBarRequest,
  socketStatusRequest,
  countdownDataRequest,
  spendingCountdownDataRequest,
  queryAlarmPowerRequest,
  queryAlarmCurrentRequest,
  deviceAccumulationParameterRequest,
  deviceRateRequest
} from '../../service/method.js'
import {
  deviceHistoryDataResponse,
  socketStatusResponse,
  countdownDataResponse,
  spendingCountdownDataResponse,
  queryAlarmPowerResponse,
  queryAlarmCurrentResponse,
  deviceAccumulationParameterResponse,
  deviceRateResponse
} from '../../service/getData.js'
if (process.env.NODE_ENV === 'development') {
  var Data = require('./data.js')
  // console.log(Data)
}
export default {
  data () {
    return {
      nav: 1, // 1 时、2 日、3 月
      powerSwitch: true,
      viewSwitch: 1, // 1 曲线图、2 表、3 柱状图
      xData: {
        month: [],
        day: [],
        hour: []
      },
      yData: {
        month: [],
        day: [],
        hour: []
      },
      progress: {
        time: '50%',
        powerConsumption: '60%',
        spend: '20%'
      },
      myChart: '', // echarts图表
      pillarEchart: '', // echarts柱状图
      maxVoltage: 0, // 最大电压
      MaximumPower: 0, // 最大功率
      electricityValue: 0, // 电量值
      eAlarmValue: 0, // 电量告警值
      spendValue: 0, // 花费值
      sAlarmValue: 0, // 花费告警值
      rate: 1, // 费率
      ratePrice: 0, // 价格
      runningTime: 0, // 运行时间
      GHG: 0, // GHG量
      clock: 0,
      realTime: {
        voltage: 0,
        electricity: 0,
        powerValue: 0,
        frequency: 0,
        powerFactor: 0
      }, // 电压 电流 功率 频率 功率因素
      timer: ''
    }
  },
  computed: {
    ...mapState([
      'currentDevice', 'statePage', 'settingData', 'jumpTimer'
    ]),
    countdown () {
      let str = this.statePage.countdownText.split('/')
      console.log(this.statePage.countdownText)
      let Percentage = ((parseInt(str[0]) * 3600 + parseInt(str[1]) * 60 + parseInt(str[2])) / (this.statePage.stateCountdown * 60)).toFixed(2) * 100
      return {
        hour: digitFormat(str[0]),
        minute: digitFormat(str[1]),
        second: digitFormat(str[2]),
        percentage: Percentage
      }
    },
    progressValue () {
      return {
        powerConsumption: parseInt((this.eAlarmValue - this.electricityValue) / this.eAlarmValue * 100) + '%',
        spend: parseInt((this.sAlarmValue - this.spendValue) / this.sAlarmValue * 100) + '%'
      }
    },
    overload () {
      return this.settingData.voltage + 'W' || this.settingData.electricity + 'A'
    },
    nowTime () {
      let date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth()
      let day = date.getDate()
      let hour = date.getHours()
      let minute = date.getMinutes()
      let second = date.getSeconds()
      clearTimeout(this.timer)
      this.clock = new Date(year, month, day, hour, minute, second)
      this.timer = setTimeout(() => {
        this.clock = new Date(year, month, day, hour, minute, ++second)
        // console.log(this.clock)
      }, 1000)
      return {
        date: `${digitFormat(this.clock.getFullYear())}
              /${digitFormat(this.clock.getMonth() + 1)}
              /${digitFormat(this.clock.getDate())}`,
        time: `${digitFormat(this.clock.getHours())}
              :${digitFormat(this.clock.getMinutes())}
              :${digitFormat(this.clock.getSeconds())}`,
        week: weekEnglish(this.clock.getDay())
      }
    },
    running () {
      let hour = parseInt(this.runningTime / 1000 / 60 / 60)
      let minute = parseInt((this.runningTime - hour * 60 * 60 * 1000) / 1000 / 60)
      let second = parseInt((this.runningTime - hour * 60 * 60 * 1000 - minute * 60 * 1000) / 1000)
      return {
        hour: digitFormat(hour),
        minute: digitFormat(minute),
        second: digitFormat(second)
      }
    }
  },
  mounted () {
    // 查询设备历史数据返回
    deviceHistoryDataResponse(this.NAVIGATOR, this)

    // 插座状态数据返回
    socketStatusResponse(this.NAVIGATOR, this)

    // 插座状态数据
    socketStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac, true)

    // 倒计时数据返回
    countdownDataResponse(this.NAVIGATOR, this)

    let timer1 = setTimeout(() => {
      // 请求倒计时数据
      countdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer1)
    }, 300)

    // 定量定费数据返回
    spendingCountdownDataResponse(this.NAVIGATOR, this)

    let timer2 = setTimeout(() => {
      // 定量定费数据请求
      spendingCountdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer2)
    }, 600)

    // 查询功率警告值返回
    queryAlarmPowerResponse(this.NAVIGATOR, this)

    let timer3 = setTimeout(() => {
      // 查询功率警告值请求
      queryAlarmPowerRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer3)
    }, 900)

    // 查询电流警告值返回
    queryAlarmCurrentResponse(this.NAVIGATOR, this)

    let timer4 = setTimeout(() => {
      // 查询电流警告值请求
      queryAlarmCurrentRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer4)
    }, 1200)

    // 查询设备累积参数返回
    deviceAccumulationParameterResponse(this.NAVIGATOR, this)

    let timer5 = setTimeout(() => {
      // 查询设备累积参数
      deviceAccumulationParameterRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer5)
    }, 1500)

    // 查询设备费率返回
    deviceRateResponse(this.NAVIGATOR, this)

    let timer6 = setTimeout(() => {
      // 查询设备费率请求
      deviceRateRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer6)
    }, 1800)

    let timer9 = setTimeout(() => {
      this.echartsInit('hour')
      clearTimeout(timer9)
    }, 350)

    // 设置手机状态栏显示
    setStatusBarRequest(this.NAVIGATOR, this, false, '')

    if (this.$root.ISDEBUG) {
      // this.makePillarEchart()
      setTimeout(() => {
        // window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":10,"allTime":10,"Switchgear":true,"countdownSwitch":true}')

        window.spendingCountdownDataResponse(this.currentDevice.mac, true, '{"power":{"currentValue":20,"alarmValue":50,"alarmSwitch":true,"year":2018,"month":6,"day":6},"cost":{"currentValue":30,"alarmValue":40,"alarmSwitch":false,"year":2018,"month":1,"day":16}}')

        window.deviceAccumulationParameterResponse(this.currentDevice.mac, 1026645, 180, 120)

        window.deviceRateResponse(this.currentDevice.mac, 1, 0, 13.8, 0, 0, 0)
      }, 3000)
    }
  },
  methods: {
    // 点击后清除跳转计时器
    clickHandle () {
      clearTimeout(this.jumpTimer)
    },

    seleteItem (num) {
      switch (num) {
        case 1:
          this.viewSwitch = num
          break
        case 2:case 3:
          this.viewSwitch = num
          break
        case 4:
          this.powerSwitch = !this.powerSwitch
          break
        case 5:
          this.$router.go(-1)
          // 插座状态数据
          socketStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac, false)
          // 设置手机状态栏显示
          setStatusBarRequest(this.NAVIGATOR, this, true, '#4EA3FE')
          break
      }
    },
    // 月份（num:0 本月 num:-1 上月 num:1 下月）
    printDate (type, num) {
      let now = new Date()
      let nowDay = now.getDate()
      let nowMonth = now.getMonth()
      let nowYear = now.getYear()
      nowYear += (nowYear < 2000) ? 1900 : 0
      let date
      let current
      let hour
      let minute
      switch (type) {
        case 'month':
          date = new Date(nowYear, nowMonth - 6, 1, 0, 0 + (num * 5))
          hour = digitFormat(date.getHours())
          minute = digitFormat(date.getMinutes())
          current = `${date.getMonth() + 1}/${date.getDate()} ${hour}:${minute}`
          return current
        case 'customMonth':
          date = new Date(nowYear, nowMonth + num, nowDay)
          return {
            year: date.getFullYear(),
            month: date.getMonth() + 1,
            day: date.getDate()
          }
        case 'day':
          date = new Date(nowYear, nowMonth, nowDay - 6, 0, 0 + (num * 5))
          hour = digitFormat(date.getHours())
          minute = digitFormat(date.getMinutes())
          current = `${date.getDate()}日 ${hour}:${minute}`
          return current
        case 'customDay':
          date = new Date(nowYear, nowMonth, nowDay + num)
          return {
            year: date.getFullYear(),
            month: date.getMonth() + 1,
            day: date.getDate()
          }
        case 'hour':
          date = num * 5
          current = numToTimeFormat(date)
          return current
      }
    },

    // echarts表导航按钮
    navHandle (num) {
      this.nav = num
      this.myChart.clear()
      switch (num) {
        case 1:
          this.makeEchart(this.xData.hour, this.yData.hour)
          break
        case 2:
          if (this.yData.day.length === 0) {
            this.echartsInit('day')
          } else {
            this.makeEchart(this.xData.day, this.yData.day)
          }
          break
        case 3:
          if (this.yData.month.length === 0) {
            this.echartsInit('month')
          } else {
            this.makeEchart(this.xData.month, this.yData.month)
          }
          break
      }
    },

    // echarts初始化
    echartsInit (type) {
      let customYear
      let customMonth
      let customDay
      let startTime
      let endTime
      let nowYear = new Date().getFullYear()
      let nowMonth = new Date().getMonth() + 1
      let nowDay = new Date().getDate()
      switch (type) {
        case 'month':
          this.xData.month = []
          this.yData.month = []
          // 设备最近7天
          customYear = this.printDate('customMonth', -2).year
          customMonth = this.printDate('customMonth', -2).month
          customDay = this.printDate('customMonth', -2).day
          startTime = `${customYear}/${customMonth}/1`
          // 到下个月开始前
          endTime = `${nowYear}/${nowMonth + 1}/1`
          // 时间段含五分钟间隔
          let length = (+new Date(endTime) - +new Date(startTime)) / 1000 / 60 / 5
          console.log(startTime)
          console.log(endTime)
          // console.log(length)
          for (let i = 0; i < length; i++) {
            this.xData.month.push(this.printDate('month', i))
            this.yData.month.push(0)
          }
          console.log(this.xData.month)
          this.makeEchart(this.xData.month, this.yData.month)
          // console.log(tomorrow)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response3')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 30, Data.monthData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'day':
          this.xData.day = []
          this.yData.day = []
          // 设备最近7天
          customYear = this.printDate('customDay', -6).year
          customMonth = this.printDate('customDay', -6).month
          customDay = this.printDate('customDay', -6).day
          startTime = `${customYear}/${customMonth}/${customDay}`
          endTime = `${nowYear}/${nowMonth}/${nowDay}`
          console.log(startTime)
          console.log(endTime)
          for (let i = 0; i < 2016; i++) {
            this.xData.day.push(this.printDate('day', i))
            this.yData.day.push(0)
          }
          this.makeEchart(this.xData.day, this.yData.day)
          // console.log(endTime)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response2')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 7, Data.dayData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'hour':
          this.xData.hour = []
          this.yData.hour = []
          // 设备24小时历史数据
          customYear = this.printDate('customDay', -1).year
          customMonth = this.printDate('customDay', -1).month
          customDay = this.printDate('customDay', -1).day
          startTime = `${customYear}/${customMonth}/${customDay}`
          endTime = `${nowYear}/${nowMonth}/${nowDay}`
          console.log(startTime)
          console.log(endTime)
          for (let i = 0; i < 288; i++) {
            this.xData.hour.push(this.printDate('hour', i))
            this.yData.hour.push(0)
          }
          this.makeEchart(this.xData.hour, this.yData.hour)
          // console.log(endTime)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response1')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
      }
    },

    // 绘制echarts折线图
    makeEchart (xData, yData) {
      this.myChart = echarts.init(document.getElementById('myChart'))
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          position: function (pos, params, dom, rect, size) {
            // 鼠标在左侧时 tooltip 显示到右侧，鼠标在右侧时 tooltip 显示到左侧。
            var obj = {top: '6%'}
            if (pos[0] < size.viewSize[0] / 2) {
              obj['right'] = '8%'
            } else {
              obj['left'] = '16%'
            }
            return obj
          },
          confine: true, // 是否将 tooltip 框限制在图表的区域内
          textStyle: {
            // fontFamily: ''
          }
        },
        legend: {
          x: '4%',
          top: '5%',
          textStyle: {
            color: 'red'
          },
          'data': ['时', '日', '月']
        },
        grid: {
          // 直角坐标系内绘图网格
          show: false, // 是否显示直角坐标系网格。[ default: false ]
          left: '15%', // grid 组件离容器左侧的距离。
          right: '10%',
          bottom: '28%',
          top: '20%'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#252525', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#252525' // 坐标值得具体的颜色
            }
            // interval: num
          },
          data: xData
        },
        yAxis: {
          type: 'value',
          name: '功率（Kw）',
          boundaryGap: false,
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#252525', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#252525' // 坐标值得具体的颜色
            }
          }
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 100,
            show: false,
            minValueSpan: 1
          }
        ],
        series: [
          {
            name: '功率（kw）',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: '#252525'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: 'transparent'
                  },
                  {
                    offset: 1,
                    color: 'transparent'
                  }
                ])
              }
            },
            data: yData
          }
        ]
      })
    },

    // 绘制echarts柱状图
    makePillarEchart (xData, yData) {
      this.pillarEchart = echarts.init(document.getElementById('Pillar'))
      this.pillarEchart.setOption({
        olor: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top: '20%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: ['13:00', '13:05', '13:10', '13:15', '13:20', '13:25', '13:30', '13:35', '13:40', '13:45', '13:50', '13:55'],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            // type : 'category',
            // data : ['10','20','30','40'],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        dataZoom: [{
          type: 'inside'
        }],
        series: [
          {
            name: '直接访问',
            type: 'bar',
            barWidth: '40%',
            data: [1, 3, 2, 3, 4, 2, 1, 3, 3, 2, 3, 2]
          }

        ],
        label: {
          normal: {
            show: true,
            position: 'top',
            formatter: '{c}'
          }
        },
        itemStyle: {
          normal: {

            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(17, 168,171, 1)'
            }, {
              offset: 1,
              color: 'rgba(17, 168,171, 0.1)'
            }]),
            shadowColor: 'rgba(0, 0, 0, 0.1)',
            shadowBlur: 10
          }
        }
      })
    }
  },

  beforeDestroy () {
    clearTimeout(this.timer)
  }
}
</script>

<style lang="less" scoped>
@import "../../style/mixin.less";
.state{
  .fs;
  z-index: 10;
  background: #FFF;
  .state-container{
    .wh(100%, 100%);
    background: url('../../image/state/wifi/background.png') 0 0 no-repeat;
    background-size: 100% 100%;
    .table{
      .wh(100%, 41.5%);
      position: relative;
      .conent{
        .wh(75%, 69%);
        position: absolute;
        left: 12%;
        bottom: 0;
        .view1, .view2, .view3, #myChart{
          .wh(100%, 100%);
          position: relative;
        }
        .view1{
          color: #252525;
          .nav{
            position: absolute;
            left: 0;
            top: 0;
            .wh-l(100%, 30px);
            .list{
              .wh(100px, 100%);
              margin: 0 auto;
              li{
                .wh(100px/3, 100%);
                float: left;
                font-size: 14px;
                position: relative;
                b{
                  content: '';
                  display: block;
                  .wh(16px, 2px);
                  background: #D18521;
                  position: absolute;
                  left: 50%;
                  bottom: 2px;
                  margin-left: -8px;
                }
              }
            }
            .time{
              .wh(80px, 100%);
              position: absolute;
              right: 0;
              top: 0;
              font-size: 12px;
              span{
                .wh(100%, 100%);
                b{
                  color: #D18521;
                  margin: 0 3px;
                }
              }
            }
          }
          .realTime{
            .wh-l(100%, 30px);
            position: absolute;
            left: 0;
            bottom: 0;
            padding: 3px 5% 0 5%;
            white-space: nowrap;
            ul{
              .wh(100%, 100%);
              li{
                .wh(20%, 100%);
                font-size: 12px;
                float: left;
                text-align: center;
                span{
                  .wh(auto, 100%);
                  max-width: 70%;
                  text-align: right;
                  display: inline-block;
                  line-height: 28px;
                  font-size: 16px;
                  font-weight: bold;
                  font-family: "lcddregular";
                  color: #141414;
                  .text-over;
                  padding: 0 2px;
                }
                strong{
                  .wh(auto, 100%);
                  display: inline-block;
                  font-family: "KozGoPro";
                  color: #000;
                  position: relative;
                  top: -8px;
                }
              }
            }
          }
        }
        .view2{
          .container{
            .wh(90%, 86%);
            .f-center;
            text-align: left;
            font-size: 15px;
            .left{
              .wh-f(60%, 100%);
              border-right: 1px solid #000000;
              li{
                .wh(100%, 16%);
                position: relative;
              }
              li:nth-child(1), li:nth-child(2) {
                height: 15%;
                color: #000;
                font-family: "KozGoPro";
                position: relative;
                top: -5px;
                span{
                  font-family: "lcddregular";
                  font-size: 18px;
                  margin-left: 5px;
                  color: #141414;
                  font-weight: bold;
                }
              }
              li:nth-child(2) {
                height: 17%;
              }
              li:nth-child(3), li:nth-child(4), li:nth-child(5) {
                height: 17%;
                position: relative;
                top: -5px;
                img{
                  height: 18px;
                  float: left;
                  position: relative;
                  top: 50%;
                  margin-top: -9px;
                }
                .progress{
                  .wh-f(46px, 9px);
                  position: relative;
                  top: 50%;
                  margin: -4px 3px 0 3px;
                  background: repeating-linear-gradient(to right,#696969 0,#696969 2px,transparent 2px, transparent 4px);
                  .per{
                    .wh(100%, 100%);
                    display: block;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background: repeating-linear-gradient(to right,#000 0,#000 2px,transparent 2px, transparent 4px);
                  }
                }
                span{
                  .wh-f(70px, 20px);
                  line-height: 20px;
                  font-family: "lcddregular";
                  font-weight: bold;
                  font-size: 14px;
                  position: relative;
                  top: 50%;
                  margin-top: -10px;
                  color: #141414;
                  white-space: nowrap;
                 b{
                   font-family: "KozGoPro";
                   font-size: 12px;
                   margin: 0 2px;
                   display: inline-block;
                   .zoom-font(0.9);
                 } 
                }
              }
              li:nth-child(6) {
                .load{
                  .wh-l(80px, 28px);
                  background: #000;
                  color: #899F75;
                  font-size: 12px;
                  float: left;
                  padding: 0 3px;
                  text-align: center;
                  .zoom-font(0.8);
                  font-family: "KozGoPro";
                  border-radius: 4px;
                }
                span{
                  .wh-l(40px, 30px);
                  display: block;
                  font-weight: bold;
                  color: #141414;
                  float: left;
                  font-family: "lcddregular";
                  font-size: 18px;
                  position: relative;
                  left: -10px;
                  b{
                    color: #000;
                    font-family: "KozGoPro";
                    margin-left: 3px;
                    font-size: 15px;
                  }
                }
              }
            }
            .right{
              .wh-f(40%, 100%);
              color: #000;
              padding-left: 10px;
              li{
                .wh(100%, 16%);
                position: relative;
              }
              li:nth-child(1){
                font-size: 14px;
                white-space: nowrap;
                font-family: "KozGoPro";
                position: relative;
                top: -5px;
              }
              li:nth-child(2){
                font-family: "lcddregular";
                position: relative;
                top: -6px;
                b{
                  font-family: "KozGoPro";
                  margin-left: 2px;
                }
              }
              li:nth-child(3){
                position: relative;
                top: -4px;
                .box{
                  .wh-l(50px, 17px);
                  border: 1px solid #000;
                  float: left;
                  .zoom-font(0.9);
                  margin-top: 5px;
                  border-radius: 2px;
                  span{
                    .wh(20%, 100%);
                    display: block;
                    background: #000;
                    color: #899F75;
                    font-size: 12px;
                    float: left;
                    font-weight: bold;
                    text-align: center;
                  }
                  strong{
                    .wh(80%, 100%);
                    display: block;
                    float: left;
                    font-size: 12px;
                    text-align: center;
                    font-family: "KozGoPro";
                  }
                }
                .num{
                  width: 30px;
                  float: left;
                  color: #141414;
                  font-family: "lcddregular";
                  font-size: 18px;
                  span{
                    font-family: "KozGoPro";
                    font-size: 14px;
                    margin-left: 3px;
                  }
                }
              }
              li:nth-child(4){
                position: relative;
                top: 0px;
                .box{
                  .wh-l(58px, 22px);
                  float: left;
                  background: #000;
                  .zoom-font(0.8);
                  color: #899F75;
                  text-align: center;
                  font-size: 12px;
                  font-family: "KozGoPro";
                  border-radius: 4px;
                }
                .num{
                  width: 30px;
                  float: left;
                  color: #141414;
                  font-family: "lcddregular";
                  font-size: 18px;
                  margin-left: -5px;
                  span{
                    font-family: "KozGoPro";
                    font-size: 14px;
                    margin-left: 3px;
                  }
                }
              }
              li:nth-child(5){
                height: 35%;
                position: relative;
                .box{
                  .wh(100px, 50px);
                  border-radius: 5px;
                  background: #000;
                  color: #899F75;
                  padding: 6px;
                  position: absolute;
                  left: 0;
                  bottom: 0;
                  .top{
                    .wh(100%, 34%);
                    position: relative;
                   span{
                     font-size: 12px;
                     font-family: "lcddregular";
                     .zoom-font(0.85);
                     display: block;
                     position: absolute;
                     left: 0;
                     top: 0;
                   }
                   strong{
                      font-size: 12px;
                      .zoom-font(0.6);
                      font-family: "KozGoPro";
                      position: absolute;
                      top: 0;
                      right: 0;
                      display: block;
                      transform-origin: right top;
                   }
                  }
                  .bottom{
                    .wh(100%, 66%);
                    font-size: 12px;
                    span{
                      .wh-f(30%, 100%);
                      display: block;
                      padding: 0 0 4px 0;
                      em{
                        .wh(100%, 50%);
                        .zoom-font(0.7);
                        display: block;
                        font-family: "KozGoPro";
                      }
                      b{
                        .wh(100%, 50%);
                        .zoom-font(0.7);
                        display: block;
                        font-family: "KozGoPro";
                      }
                    }
                    strong{
                      .wh-f(70%, 100%);
                      display: block;
                      position: relative;
                      em{
                        font-size: 20px;
                        font-family: "lcddregular";
                        .f-center;
                        font-weight: bold;
                        margin-top: 3px;
                        white-space: nowrap;
                        left: 46%;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .view3{
          #Pillar{
            .wh(100%, 100%);
          }
          .nav{
            .wh-l(180px, 24px);
            position: absolute;
            top: 5%;
            right: 5%;
            font-size: 12px;
            background: #141414;
            border-radius: 4px;
            color: #BBD9A0;
            transform-origin: top right;
            transform: scaleX(0.8);
            ul{
              .wh(100%, 100%);
              li{
                .wh-f(25%, 100%);
                position: relative;
                &:after{
                  content: '';
                  display: block;
                  .wh(1px, 12px);
                  background: #BBD9A0;
                  position: absolute;
                  top: 50%;
                  right: 1%;
                  margin-top: -6px;
                }
              }
              li:last-child{
                &:after{
                  background: transparent;
                }
              }
            }
          }
        }
      }
    }
    .button{
      .wh(100%, 50%);
      position: absolute;
      left: 0;
      bottom: 0;
      ul{
        .wh(60%, 77.5%);
        position: absolute;
        left: 20%;
        top: 0;
        li{
          .wh(42.5%, 18%);
          display: block;
          img{
            .wh(100%, 100%);
          }
        }
        li:nth-child(1) {
          position: absolute;
          left: 0;
          top: 0;
        }
        li:nth-child(2) {
          position: absolute;
          right: 0;
          top: 0;
        }
        li:nth-child(3) {
          .wh(35%, 29.4%);
          .f-center;
          top: 51%;
        }
        li:nth-child(4) {
          position: absolute;
          left: 0;
          bottom: 0;
          span{
            .wh(16%, 16%);
            display: block;
            position: absolute;
            left: 42%;
            top: -48%;
            img{
              .wh(100%, auto);
              float: left;
            }
            img:nth-child(1) {
              position: relative;
              top: -2px;
            }
          }
        }
        li:nth-child(5) {
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }
    }
  }  
}
</style>



