let timer
const app = {
  state: {
    loading: false
  },
  mutations: {
    LOADING_ALERT: (state, bol) => {
      state.loading = bol
    }
  },
  actions: {
    loadingAlert ({commit, state, rootState}, bol) {
      commit('LOADING_ALERT', bol)
      if (bol) {
        timer = setTimeout(() => {
          state.loading = false
          rootState.ToastType = 'overtime'
          clearTimeout(timer)
        }, 20000)
      } else {
        clearTimeout(timer)
      }
    }
  }
}

export default app



