<template>
  <div class="rename">
    <head-top title="Change Name" goBack="true">
      <div slot="setRight" class="setRight Save" v-if="value===''||value===undefined">Save</div>
      <div slot="setRight" class="setRight" v-else @click="save">Save</div>
    </head-top>
    <div class="rename_container">
      <div class="column">
        <div class="top-pixel blue"></div>
        <input type="text" v-model="value" placeholder="Only letters and numbers allowed">
        <span>1-16 characters
          <div class="top-pixel blue"></div>
        </span>
      </div>
    </div>
    <toast describe="Only letters and numbers allowed!" v-show="ToastType==='allowed'" position="bottom"></toast>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import headTop from '../../components/header/header'
import toast from '../../components/common/toast'
import { modifyUsernameRequest } from '../../service/method.js'
import { vEnglishName } from '../../config/utils.js'

export default {
  data () {
    return {
      value: ''
    }
  },

  mounted () {
    if (this.$route.params.type === 1) {
      this.value = this.userinfo.firstName
    } else {
      this.value = this.userinfo.lastName
    }
  },

  components: {
    headTop,
    toast
  },

  computed: {
    ...mapState([
      'ToastType'
    ]),
    ...mapGetters([
      'userinfo'
    ])
  },

  methods: {
    ...mapActions([
      'setUserinfo', 'setResponseType'
    ]),

    save () {
      console.log(vEnglishName(this.value))
      if (vEnglishName(this.value)) {
        this.setResponseType('rename')
        if (this.$route.params.type === 1) {
          modifyUsernameRequest(this.NAVIGATOR, this, 'name', this.value)
        } else {
          modifyUsernameRequest(this.NAVIGATOR, this, 'surnam', this.value)
        }
        if (this.$root.ISDEBUG) {
          let obj
          if (this.$route.params.type === 1) {
            obj = {'firstName': this.value}
            window.modifyUserInformationResponse(true, JSON.stringify(obj))
          } else {
            obj = {'lastName': this.value}
            window.modifyUserInformationResponse(true, JSON.stringify(obj))
          }
        }
      } else {
        this.$store.state.ToastType = 'allowed'
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.rename{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .setRight{
    text-align: right;
    padding-right: 20px;
  }
  .Save{
    color: #b0d6ff;
  }
  .rename_container{
    .fs-noTop;
    .column{
      .wh(100%, 90px);
      position: relative;
      margin-top: 27px;
      input{
        .wh-l(100%, 50px);
        display: block;
        text-align: left;
        padding: 0 5%;
      }
      span{
        position: relative;
        .wh-l(100%, 40px);
        display: block;
        text-align: left;
        font-size: 12px;
        padding: 0 5%;
        color: #CCC;
      }
      .blue{
        background: @themeColor;
      }
    }
  }
}
</style>



