<template>
  <div class="personal">
    <head-top :title="$t('userPage.changeProfile')" goBack="true"></head-top>
    <div class="personal_container">
      <!-- {{avater}} -->
      <div class="pic" @click="setAvatarHandle">
        <span>{{$t('userPage.pic')}}</span>
        <div class="image" v-if="$store.state.LoginState" :style="{backgroundImage: 'url(' + userinfo.headPic + ')'}"></div>
        <div class="pic" v-else></div>
      </div>
      <!-- 英文版 -->
      <div class="column" v-show="$i18n.locale==='en'">
        <router-link :to="{name: 'personalRename', params: {type: 1}}">
          <span>Name</span>
          <strong>{{userinfo.firstName}}</strong>
        </router-link>
        <div class="bom-pixel"></div>
      </div>
      <div class="column" v-show="$i18n.locale==='en'">
        <router-link :to="{name: 'personalRename', params: {type: 2}}">
          <span>Surname</span>
          <strong>{{userinfo.lastName}}</strong>
        </router-link>
      </div>
      <div class="column noNext" v-show="$i18n.locale==='en'">
        <a>
          <span>Email</span>
          <strong>{{userinfo.email}}</strong>
        </a>
      </div>
      <!-- 中文版 -->
      <div class="column noNext" v-show="$i18n.locale==='zh'">
        <a>
          <span>手机</span>
          <strong>{{userinfo.mobile}}</strong>
        </a>
      </div>
      <div class="themeButton logOut" @click="logout">
        <span>{{$t('homePage.Logout')}}</span>
      </div>
    </div>

    <!-- 操作表 -->
    <actionSheet setHeight="195" v-show="actionSheetType == 'modifyAvatar'">
      <ul class="modifyAvatar" slot="main">
        <li>
          <a>{{$t('userPage.changePic')}}</a>
          <div class="bom-pixel"></div>
        </li>
        <li @click="sheetItem(1)">
          <a>{{$t('userPage.takePhoto')}}</a>
          <div class="bom-pixel"></div>
        </li>
        <li @click="sheetItem(2)">
          <a>{{$t('userPage.phoneAlbum')}}</a>
          <div class="bom-pixel"></div>
        </li>
        <li @click="sheetItem(0)">
          <a>{{$t('userPage.cancel')}}</a>
        </li>
      </ul>
    </actionSheet>

    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='logoutOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('userPage.logoutText')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>

    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
import headTop from '../../components/header/header'
import actionSheet from '../../components/common/actionSheet'
import messageBox from '../../components/common/messageBox'
import {
  logoutUserRequest,
  takePhotoRequest,
  localPhotoRequest
} from '../../service/method.js'
import {
  modifyAvatarSendedResponse
} from '../../service/getData.js'

export default {
  data () {
    return {}
  },

  mounted () {
    // 修改头像数据发送返回
    modifyAvatarSendedResponse(this.NAVIGATOR, this)

    // setTimeout(() => {
    //   window.modifyUserInformationResponse(true,'{"headPic":"http:\/\/file.startai.com.cn:3108\/group1\/M00\/00\/0E\/Zy1orFuR9IOEFlc6AAAAAAAAAAA178.jpg"}')
    //   console.log(this.userinfo)
    // }, 4000)
  },

  components: {
    headTop,
    actionSheet,
    messageBox
  },

  computed: {
    ...mapState([
      'actionSheetType', 'messageBoxType'
    ]),

    ...mapGetters([
      'userinfo'
    ])
    // avater () {
    //   return this.$set(this.$store.state.user.userinfo, 'headPic', '')
    //   this.$store.state.user.userinfo.headPic
    // }
  },

  methods: {
    ...mapMutations([
      'TABLE_SWITCH', 'MESSAGEBOX_SWITCH'
    ]),

    ...mapActions([
      'setUserinfo', 'setResponseType', 'loadingAlert'
    ]),

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: 'logoutOpreation', bol: false})
          break
        case 1:
          this.MESSAGEBOX_SWITCH({type: 'logoutOpreation', bol: false})
          logoutUserRequest(this.NAVIGATOR, this)

          if (this.$root.ISDEBUG) {
            window.logoutUserResponse(true)
          }
          break
      }
    },

    // 修改头像
    setAvatarHandle () {
      this.TABLE_SWITCH({type: 'modifyAvatar', flag: true})
    },

    sheetItem (num) {
      switch (num) {
        case 0:
          break
        case 1:
          // 手机拍照
          takePhotoRequest(this.NAVIGATOR, this)
          break
        case 2:
          // 本地照片
          localPhotoRequest(this.NAVIGATOR, this)
          break
      }
      this.TABLE_SWITCH({type: this.actionSheetType, flag: false})
    },

    // 注销
    logout () {
      this.MESSAGEBOX_SWITCH({type: 'logoutOpreation', bol: true})
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.personal{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .personal_container{
    .fs-noTop;
    background: #F2F2F2;
    font-size: 17px;
    .pic{
      .wh-l(100%, 110px);
      position: relative;
      padding: 0 5%;
      background: #FFF;
      margin-bottom: 15px;
      span{
        .wh-f(50%, 100%);
        display: block;
        text-align: left;
      }
      .image, .pic{
        height: 58px;
        width: 58px;
        position: absolute;
        top: 50%;
        right: 5%;
        margin-top: -29px;
        border-radius: 50%;
        background: url(../../image/user/head.png) 0 0 no-repeat;
        background-size: cover;
      }
    }
    .column{
      .wh-l(100%, 45px);
      background: #FFF;
      position: relative;
      a{
        .wh(100%, 100%);
        display: block;
        padding: 0 5%;
        span{
          .wh-f(30%, 100%);
          display: block;
          text-align: left;
        }
        strong{
          .wh-f(70%, 100%);
          display: block;
          text-align: right;
          padding-right: 10%;
          position: relative;
          color: #999999;
          &:after{
            content: '';
            .wh(18px, 18px);
            display: block;
            background: url('../../image/global/next.png') 0 0 no-repeat;
            background-size: cover;
            position: absolute;
            top: 50%;
            right: -6px;
            margin-top: -9px;
          }
        }
      }
    }
    .noNext{
      margin-top: 15px;
      a{
        strong{
          padding-right: 0%;
          &:after{
            background: none;
          }
        }
        &:active{
          background: transparent;
        }
      }
    }
    .logOut{
      margin: 45px auto;
    }
  }
  .modifyAvatar{
    .wh(100%, 100%);
    li{
      .wh-l(100%, 50px);
      position: relative;
      a{
        .wh-l(100%, 50px);
        font-size: 15px;
        display: block;
        color: #555;
      }
    }
    li:first-child{
      .wh-l(100%, 45px);
      a{
        .wh-l(100%, 45px);
        font-weight: bold;
        &:active{
          background: transparent;
        }
      }
    }
  }
}
</style>



