/**
 * 手机物理按键后退操作 Router
 * @method goBackResponse
 * @param  {string}   navigator   设备类型，必选
 * @param  {object}   self        this对象，必选
 */

export const goBackResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.goBackResponse = function () {
        if (self.currentRouter === '/add') {
          console.log('2018081822')
          window.Router.goBackRequest()
          return false
        }
        console.log('20180811')
        console.log(self.currentRouter)
        if (self.VERSION === 1 && self.currentDevice && !self.currentDevice.state) {
          self.$router.push({ path: '/add' })
          self.$store.state.firstRunning = false
          console.log('self.$router.push({ path:  / add })')
          return false
        }
        console.log(' return false')
        if (self.actionSheet) {
          self.TABLE_SWITCH({
            type: self.actionSheetType
          })
          return false
        }
        if (self.messageBox) {
          self.MESSAGEBOX_SWITCH({
            type: self.messageBoxType
          })
          return false
        }
        if (self.ToastType) {
          if (self.$store.state.ToastType === 'addEquipment') {
            console.log('disconnect')
            // 断开连接
            equipmentSwitchRequest(
              self.NAVIGATOR,
              self,
              self.$store.state.currentDevice.mac,
              0
            )
          }
          return false
        }
        // 主界面退出操作
        if (self.VERSION === 1) {
          if (self.currentRouter === '/main' || self.currentRouter === '/add') {
            window.Router.goBackRequest()
            return false
          }
        } else if (self.VERSION === 2) {
          if (self.currentRouter === '/home') {
            window.Router.goBackRequest()
            return false
          }
        }

        if (self.currentRouter === '/main/eq') {
          closeSearchRequest(self.NAVIGATOR, self)
          self.searchStop = false
        }
        // 设备列表界面停止扫描操作
        if (self.currentRouter === '/add/connect') {
          closeSearchRequest(self.NAVIGATOR, self)
          self.searchStop = false
        }
        // 数据表返回后断开连接
        if (self.currentRouter === '/add/state') {
          // equipmentSwitchRequest(
          //   self.NAVIGATOR,
          //   self,
          //   self.$store.state.currentDevice.mac,
          //   0
          // )
          // self.$store.state.currentDevice = null
          self.$router.push({
            path: '/main'
          })
          return false
        }

        if (self.currentRouter === '/home/adddirver/connectwifi') {
          self.MESSAGEBOX_SWITCH({type: 'exitConnect'})
          return false
        }
        if (self.currentRouter === '/home/adddirver/choose') {
          // 关闭路由器扫描设备
          closeDeviceScanningRequest(self.NAVIGATOR, self)
          if (self.$store.state.findPage) {
            self.$router.go(-1)
          } else {
            self.$router.go(-2)
          }
          return false
        }
        if (self.currentRouter === '/home/adddirver/addsuccess') {
          self.$router.push({ path: '/home' })
          return false
        }
        if (self.currentRouter === '/home/adddirver/rename') {
          return false
        }
        if (self.currentRouter === '/main/wifiState') {
          // 设置手机状态栏显示
          setStatusBarRequest(self.NAVIGATOR, self, true, '#4EA3FE')
        }
        self.$router.go(-1)
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 判断蓝牙是否开启返回 Device
 * @method isOpenBluetoothResponse
 * @param  {string}   navigator   设备类型，必选
 * @param  {object}   self        this对象，必选
 */

export const isOpenBluetoothResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.isOpenBluetoothResponse = function (result) {
        if (result) {
          isFirstBindingRequest(self.NAVIGATOR, self)
          self.$router.push({path: '/add/connect'})
          self.$store.firstRunning = true
        } else {
          self.MESSAGEBOX_SWITCH({type: 'openbluetooth'})
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 判断是否首次绑定返回 Device
 * @method isFirstBindingResponse
 * @param  {string}   navigator   设备类型，必选
 * @param  {object}   self        this对象，必选
 */

export const isFirstBindingResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.isFirstBindingResponse = function (result, data) {
        if (!result) {
          self.$router.push({name: 'connect', params: {connect: true, data: data}})
          // 储存当前设备
          self.$store.state.currentDevice = JSON.parse(data)
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

