/**
 * 倒计时页面数据 Countdown
 * @method countdownDataResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {string}    data        倒计时页面数据返回
 */

export const countdownDataResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.countdownDataResponse = function (mac, data) {
        let obj = JSON.parse(data)
        // 执行倒计时
        if (self.$store.state.statePage.Timer === null && parseInt(obj.allTime) > 0) {
          clearInterval(self.$store.state.statePage.Timer)
          // let countDownRange = JSON.parse(getStore('countDownRange'))
          // if (countDownRange) {
          //   self.$store.state.statePage.stateCountdown = countDownRange.num
          // }
          let h = obj.hour
          let m = obj.minute
          let s = 60
          console.log('20180830')
          self.$store.state.statePage.stateCountdown = h * 60 + m
          console.log(self.$store.state.statePage.stateCountdown)
          self.$store.state.statePage.Timer = setInterval(() => {
            if (h === 0 && m === 0) {
              clearInterval(self.$store.state.statePage.Timer)
              self.$store.state.statePage.stateCountdown = 0
              self.$store.state.statePage.countdownTimeText = '00:00'
              self.$store.state.statePage.countdownRotate = 0
              return false
            } else if (m <= 0) {
              h--
              m = 59
            } else if (s <= 0) {
              m--
              console.log('20170718')
              s = 60
            }
            s--
            let runningSecond = h * 60 * 60 + m * 60 + s
            self.$store.state.statePage.countdownRotate = (runningSecond - 59) / (self.$store.state.statePage.stateCountdown * 60) * 180
            self.$store.state.statePage.countdownText = `${h}/${m}/${s}`
            // console.log(self.$store.state.statePage.countdownText)
            // self.$store.state.statePage.countdownTimeText = `${digitFormat(h)}:${digitFormat(m)}`
            // console.log(m, ':', s)
            // console.log(self.$store.state.statePage.countdownTimeText)
          }, 1000)
        }
        // else {
        //   clearInterval(self.$store.state.statePage.Timer)
        //   self.$store.state.statePage.stateCountdown = 0
        //   self.$store.state.statePage.countdownTimeText = '00:00'
        //   self.$store.state.statePage.countdownRotate = 0
        // }
        console.log(page)
        // 是否为插座新状态界面
        if (page === 'blueToothState') {
          if (self.currentDevice.mac === mac) {
            console.log(obj)
            self.countdownSwitch = obj.countdownSwitch
          }
        }
        if (self.currentDevice.mac === mac) {
          let obj = JSON.parse(data)
          setStore('countDownData', obj)
          self.localTime = obj
          self.time = obj.hour + ':' + obj.minute
          self.$store.state.countdownValue = obj.hour * 60 + obj.minute
          self.$store.state.startActive = obj.countdownSwitch // 启动结束
          self.$store.state.countDownOFF = obj.countdownSwitch
          self.$store.state.statePage.countdownTimeText = `${digitFormat(obj.hour)}:${digitFormat(obj.minute)}`
          // self.powerActive = obj.Switchgear     // 开机关机
          console.log(typeof obj.hour)
          if (obj.hour === 0 && obj.minute === 0) {
            powerSwitchStatusRequest(self.NAVIGATOR, self, self.currentDevice.mac)
            self.$store.state.socketSwitch = obj.Switchgear
            clearInterval(self.$store.state.statePage.Timer)
            self.$store.state.statePage.stateCountdown = 0
            self.$store.state.statePage.countdownTimeText = '00:00'
            self.$store.state.statePage.countdownRotate = 0
            // if (self.$root.ISDEBUG) {
            //   setTimeout(() => {
            //     window.powerSwitchResponse(self.currentDevice.mac, false)
            //     console.log('powerSwitchResponse开启')
            //   }, 4000)
            // }
          }
          if (!obj.countdownSwitch) {
            self.$store.state.powerActive = !self.socketSwitch
          } else {
            self.$store.state.powerActive = obj.Switchgear
            let countDownRange = {}
            countDownRange.num = parseInt(obj.allTime)
            countDownRange.mac = mac
            setStore('countDownRange', countDownRange)
          }
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 设置电源开关倒计时返回 Countdown
 * @method powerSwitchCountdownResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {boolean}   state      电源开关状态返回
 * @param  {boolean}   result     插座操作状态返回:true 成功、 false 失败
 */

export const powerSwitchCountdownResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.powerSwitchCountdownResponse = function (mac, state, result) {
        if (self.currentDevice.mac === mac && result) {
          self.$store.state.startActive = state
          self.$store.state.countDownOFF = state
          // 点击结束后，本地总时长置零
          if (!state) {
            clearInterval(self.$store.state.statePage.Timer)
            self.$store.state.stateCountdown = 0
            self.$store.state.statePage.countdownRotate = 0
            self.$store.state.statePage.countdownTimeText = '00:00'
          } else {
            let array = self.time.split(':')
            let countDownRange = {}
            self.countDownRange = parseInt(array[0]) * 60 + parseInt(array[1])
            countDownRange.num = parseInt(array[0]) * 60 + parseInt(array[1])
            countDownRange.mac = mac
            countDownRange.hour = parseInt(array[0])
            countDownRange.minute = parseInt(array[1])
            // console.log('201807171412')
            setStore('countDownRange', countDownRange)
            self.$store.state.countdownValue = parseInt(array[0]) * 60 + parseInt(array[1])
            // 倒计时
            clearInterval(self.$store.state.statePage.Timer)
            let h = parseInt(array[0])
            let m = parseInt(array[1])
            let s = 60
            // console.log('20170719')
            self.$store.state.statePage.stateCountdown = parseInt(array[0]) * 60 + parseInt(array[1])
            self.$store.state.statePage.Timer = setInterval(() => {
              if (h === 0 && m === 0) {
                clearInterval(self.$store.state.statePage.Timer)
                self.$store.state.statePage.stateCountdown = 0
                self.$store.state.statePage.countdownTimeText = '00:00'
                self.$store.state.statePage.countdownRotate = 0
                return false
              } else if (m <= 0) {
                h--
                m = 59
              } else if (s <= 0) {
                m--
                // console.log('20170718')
                s = 60
              }
              s--
              // console.log(s)
              // console.log(self.$store.state.statePage.Timer)
              let runningSecond = h * 60 * 60 + m * 60 + s
              // console.log(runningSecond)
              self.$store.state.statePage.countdownRotate = (runningSecond - 59) / (self.$store.state.statePage.stateCountdown * 60) * 180
              // console.log('20170723')
              // console.log(self.$store.state.statePage.countdownRotate)
              // 倒计时时间
              // self.$store.state.statePage.countdownTimeText = `${digitFormat(h)}:${digitFormat(m)}`
              // console.log(m, ':', s)
              // console.log(self.$store.state.statePage.countdownTimeText)
            }, 1000)
          }
        } else {
          self.$store.state.ToastType = 'setError'
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 温湿度页面数据 temperatureAndHumidity
 * @method temperatureAndHumidityDataResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {string}    data         温湿度数据返回
 */

export const temperatureAndHumidityDataResponse = (navigator, self, page) => {
  // 保留一位小数
  function sign (val) {
    return parseFloat((val * 1.8 + 32).toFixed(1))
  }
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.temperatureAndHumidityDataResponse = function (mac, data) {
        // 是否为插座新状态页
        if (page === 'blueToothState') {
          if (self.currentDevice.mac === mac) {
            let obj = JSON.parse(data).temperature
            let currentValue
            let hotAlarmValue
            let codeAlarmValue
            console.log(obj)
            if (self.TemperatureUnit === 1) {
              currentValue = parseFloat(obj.currentValue)
              hotAlarmValue = parseInt(obj.hotAlarmValue)
              codeAlarmValue = parseInt(obj.codeAlarmValue)
              // self.$store.state.tTargetValue = 26
            } else {
              currentValue = sign(obj.currentValue)
              hotAlarmValue = Math.round(obj.hotAlarmValue * 1.8 + 32)
              codeAlarmValue = Math.round(obj.codeAlarmValue * 1.8 + 32)
              // self.$store.state.tTargetValue = 79
            }
            console.log(currentValue)
            // 制冷制热
            if (obj.hotAlarmSwitch) {
              self.$store.state.isHeating = true
              // 目标温度
              self.$store.state.tTargetValue = hotAlarmValue
            } else if (obj.codeAlarmSwitch) {
              self.$store.state.isHeating = false
              self.$store.state.tTargetValue = codeAlarmValue
            } else {
              self.$store.state.isHeating = false
            }
          }
        } else if (page === 'home') {
          return false
        }
        if (self.currentDevice.mac === mac) {
          let obj = JSON.parse(data)
          setStore('temperatureData', obj)
          if (self.TemperatureUnit === 1) {
            if (obj.temperature.codeAlarmSwitch) {
              // self.hAlarmValue = parseInt(obj.temperature.codeAlarmValue)
              self.$store.state.tTargetValue = parseInt(obj.temperature.codeAlarmValue)
            } else if (obj.temperature.hotAlarmSwitch) {
              // self.tAlarmValue = parseInt(obj.temperature.hotAlarmValue)
              self.$store.state.tTargetValue = parseInt(obj.temperature.hotAlarmValue)
            } else {
              self.$store.state.tTargetValue = '--'
            }
            self.hAlarmValue = parseInt(obj.temperature.codeAlarmValue)
            self.tAlarmValue = parseInt(obj.temperature.hotAlarmValue)
            self.temperatureValue = parseFloat(obj.temperature.currentValue)
            self.humidityValue = parseFloat(obj.temperature.currentValue)
            // 当前温度
            self.$store.state.currentTemperature = parseFloat(obj.temperature.currentValue)
          } else if (self.TemperatureUnit === 2) {
            if (obj.temperature.codeAlarmSwitch) {
              // self.hAlarmValue = Math.round(obj.temperature.codeAlarmValue * 1.8 + 32)
              self.$store.state.tTargetValue = Math.round(obj.temperature.codeAlarmValue * 1.8 + 32)
            } else if (obj.temperature.hotAlarmSwitch) {
              // self.tAlarmValue = Math.round(obj.temperature.hotAlarmValue * 1.8 + 32)
              self.$store.state.tTargetValue = Math.round(obj.temperature.hotAlarmValue * 1.8 + 32)
            } else {
              self.$store.state.tTargetValue = '--'
            }
            self.hAlarmValue = Math.round(obj.temperature.codeAlarmValue * 1.8 + 32)
            self.tAlarmValue = Math.round(obj.temperature.hotAlarmValue * 1.8 + 32)
            self.temperatureValue = sign(obj.temperature.currentValue)
            self.humidityValue = sign(obj.temperature.currentValue)
            self.$store.state.currentTemperature = sign(obj.temperature.currentValue)
          }

          self.$store.state.hStartActive = obj.temperature.codeAlarmSwitch
          self.$store.state.tStartActive = obj.temperature.hotAlarmSwitch
          self.$store.state.temperatureOFF = obj.temperature.codeAlarmSwitch || obj.temperature.hotAlarmSwitch

          if (self.temperatureMode) {
            self.tempData = obj
            if (obj.temperature.codeAlarmSwitch && !obj.temperature.hotAlarmSwitch) {
              self.temperatureMode = self.$t('setTaskPage.coolRun')
              self.$store.state.isHeating = false
            } else if (!obj.temperature.codeAlarmSwitch && obj.temperature.hotAlarmSwitch) {
              self.temperatureMode = self.$t('setTaskPage.heaterRun')
              self.$store.state.isHeating = true
            }
          }
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 设置告警温度值返回 temperatureAndHumidity
 * @method alarmTemperatureValueResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {boolean}   state       true 启动、 false 结束
 * @param  {boolean}   result      插座操作状态返回:true 成功、 false 失败
 * @param  {number}    mode        1 加热模式返回 2 制冷模式返回
 */

export const alarmTemperatureValueResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.alarmTemperatureValueResponse = function (mac, state, mode, result) {
        if (self.currentDevice.mac === mac && result) {
          switch (mode) {
            case 1:
              self.$store.state.tStartActive = state
              self.$store.state.temperatureOFF = state
              if (page === 'humidity') {
                console.log(self.tAlarmValue)
                self.$store.state.tTargetValue = self.tAlarmValue
                self.$store.state.isHeating = state
              }
              break
            case 2:
              self.$store.state.hStartActive = state
              self.$store.state.temperatureOFF = state
              if (page === 'humidity') {
                self.$store.state.tTargetValue = self.hAlarmValue
              }
              break
            default:
              break
          }
        } else {
          self.$store.state.ToastType = 'setError'
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

