/**
 * 定时列表数据返回 commonPatternListDataResponse
 * @method alarmHumidityValueResponse
 * @param  {string}    navigator   设备类型，必选
 * @param  {object}    self        this对象，必选
 * @param  {string}    mac         mac地址
 * @param  {string}   data         定时列表数据返回
 */
export const commonPatternListDataResponse = (navigator, self, page) => {
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.commonPatternListDataResponse = function (mac, ordinaryData, advancedData) {
        if (page === 'blueToothState') {
          if (self.currentDevice.mac === mac) {
            if (ordinaryData === '') {
              ordinaryData = '[]'
            }
            if (advancedData === '') {
              advancedData = '[]'
            }
            self.$store.state.ordinaryData = JSON.parse(ordinaryData)
            self.advancedData = JSON.parse(advancedData)
          }
        }
        if (self.currentDevice.mac === mac) {
          let flag = false
          let timingdate = (JSON.parse(ordinaryData))
          if (timingdate.length > 0) {
            timingdate.forEach(element => {
              if (element.state) {
                self.$store.state.timingOff = true
                flag = true
                // self.taskTime.push(element.time.split(':')[0] + element.time.split(':')[1])
              }
              if (!flag) {
                self.$store.state.timingOff = false
              }
            })
          } else {
            self.$store.state.timingOff = false
          }
          console.log(self.currentDevice)
          if (self.dataForamt) {
            self.timeData = self.dataForamt(JSON.parse(ordinaryData))
            self.seniorTimeData = self.dataForamt(JSON.parse(advancedData))
            let obj = self.timeData
            // let timingObj = {
            //   mac: mac,
            //   data: self.timeData
            // }
            setStore('timeData', obj)
          }
          if (page === 'blueToothState') {
            self.clockPointer()
          }
          self.seniorTimeDat = JSON.parse(advancedData)
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

