/**
 * 定量定费                     SpendingCountdown
 * 定量定费告警设置返回          spendingCountdownAlarmResponse
 * @method                     spendingCountdownAlarmResponse
 * @param         {string}     mac          mac地址
 * @param         {boolean}    result      定量定费告警设置返回结果
 * @param         {boolean}    state      true 启动 false 结束
 * @param         {number}     mode       1定电量 2定花费
 */
export const spendingCountdownAlarmResponse = (navigator, self) => {
  console.log('定量定费告警设置返回')
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.spendingCountdownAlarmResponse = function (mac, mode, state, result) {
        if (self.currentDevice.mac === mac && result) {
          console.log('定量定费告警设置成功')
          switch (mode) {
            case 1:
              self.eStartActive = state
              self.$store.state.spendingCountdownOff = state
              if (self.spendingCountdownMode) {
                self.spendingCountdownMode = '电量设定已启动'
              }
              break
            case 2:
              self.sStartActive = state
              self.$store.state.spendingCountdownOff = state
              if (self.spendingCountdownMode) {
                self.spendingCountdownMode = '花费设定已启动'
              }
              break
            default:
              break
          }
        } else {
          self.$store.state.ToastType = 'setError'
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

/**
 * 定量定费           SpendingCountdown
 * 定量定费数据返回   spendingCountdownDataResponse
 * @method           spendingCountdownDataResponse
 * @param         {string}     mac          mac地址
 * @param         {boolean}    result      定量定费数据返回结果
 * @param         {string}     data        页面数据
 */
export const spendingCountdownDataResponse = (navigator, self) => {
  console.log('定量定费数据返回')
  switch (navigator) {
    case 'IOS':
    case 'Android':
      // console.log(navigator)
      window.spendingCountdownDataResponse = function (mac, result, data) {
        if (self.currentDevice.mac === mac && result) {
          let obj = JSON.parse(data)
          let pm = obj.power.month < 10 ? '0' + obj.power.month : obj.power.month
          let pd = obj.power.day < 10 ? '0' + obj.power.day : obj.power.day
          let cm = obj.cost.month < 10 ? '0' + obj.cost.month : obj.cost.month
          let cd = obj.cost.day < 10 ? '0' + obj.cost.day : obj.cost.day

          self.electricityValue = obj.power.currentValue
          self.eAlarmValue = obj.power.alarmValue
          self.eStartActive = obj.power.alarmSwitch
          self.eStartTime = obj.power.year + '-' + pm + '-' + pd

          self.spendValue = obj.cost.currentValue
          self.sAlarmValue = obj.cost.alarmValue
          self.sStartActive = obj.cost.alarmSwitch
          self.sStartTime = obj.cost.year + '-' + cm + '-' + cd

          if (self.spendingCountdownMode) {
            self.spendcostData = obj
            // self.$store.state.spendingCountdownOff = true
            if (obj.cost.alarmSwitch && !obj.power.alarmSwitch) {
              self.spendingCountdownMode = '花费设定已启动'
            } else if (!obj.cost.alarmSwitch && obj.power.alarmSwitch) {
              self.spendingCountdownMode = '电量设定已启动'
            } else if (obj.cost.alarmSwitch && obj.power.alarmSwitch) {
              self.spendingCountdownMode = '定量定费已启动'
            }
          }
        }
      }
      break
    case 'Wechat':
      // console.log('Wechat')
      break
  }
}

