let timer
export const switchNetworkResponse = (navigator, self) => {
  switch (navigator) {
    case 'IOS':case 'Android':
      // console.log('IOS')
      try {
        window.switchNetworkResponse = function (type, state) {
          if (self.currentRouter === '/home/adddirver/configwifi') {
            if (type === 'WIFI') {
              // 获取wifi名称
              WiFiNameRequest(self.NAVIGATOR, self)
            }
          } else if (self.currentRouter === '/home/adddirver/connectwifi') {
            self.$store.state.ToastType = 'networkSwitch'
            self.$router.go(-1)
            stopConnectionNetworkRequest(self.NAVIGATOR, self)
            // 获取wifi名称
            WiFiNameRequest(self.NAVIGATOR, self)
          }
          clearTimeout(timer)
          if (state === 1 && self.$store.state.LoginState) {
            timer = setTimeout(() => {
              // 查询用户信息
              userInformationRequest(self.NAVIGATOR, self)
              clearTimeout(timer)
            }, 1500)
          }
        }
      } catch (err) {
        console.log(err)
      }
      break
    case 'Wechat':
      // console.log("Wechat")
      break
  }
}

