<template>
  <div class="about">
    <head-top :title="$t('userPage.aboutUS')" goBack="true"></head-top>
    <div class="about_container">
      <div class="column">
        <img src="../../image/user/WiFi.png" alt="">
        <span>{{$t('userPage.wifiSocket')}}</span>
        <em>{{version}}</em>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import { versionNumberRequest } from '../../service/method.js'
import { versionNumberResponse } from '../../service/getData.js'

export default {
  data () {
    return {
      version: ''
    }
  },

  mounted () {
    // 版本号返回
    versionNumberResponse(this.NAVIGATOR, this)

    // 版本号请求
    versionNumberRequest(this.NAVIGATOR, this)
  },

  components: {
    headTop
  },

  computed: {}
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../style/mixin.less';

.about{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .about_container{
    .fs-noTop;
    .column{
      .wh(180px, 230px);
      .f-center;
      top: 42%;
      img{
        height: 140px;
        margin: 0 auto;
      }
      span{
        font-size: 18px;
        display: block;
        margin: 6px auto 0 auto;
      }
      em{
        font-size: 15px;
        display: block;
        margin: 19px auto 0 auto;
      }
    }
  }
}
</style>



