<template>
  <div class="home">
    <div class="home_container">
      <div class="statusBar"></div>
      <div class="equipment" v-show="foot_active===1">
        <div class="header">
          {{$t('homePage.deviceList')}}
          <div class="add" @click="add">
            <img src="../image/home/Plus.png">
          </div>
        </div>
        <div class="no-network" v-if="!$store.state.noNetwork">
          <strong>
            <img src="../image/home/disconnect.png" alt="">
            <span>{{$t('homePage.networkDisconnected')}}</span>
          </strong>
        </div>
        <div class="load_more" v-show="deviceList.length&&deviceList">
          <scroll :data="deviceList"
                  :pullRefreshObj="pullRefreshObj"
                  :listenScroll="listenScroll"
                  @pulldown="refreshData"
                  @pullup="loadData"
                  @scroll="scrollHandle">
            <slider :data="deviceList"
                    :itemHeight="105"
                    @skip="goDevice"
                    @delete="deleteDevice">
              <div slot-scope="slotProps" class="eq-column">
                <a>
                  <div class="column-msg">
                    <div class="col_left">
                      <img v-show="slotProps.item.switch" src="../image/home/icon-click.png">
                      <img v-show="!slotProps.item.switch" src="../image/home/icon-default.png">
                    </div>
                    <div class="col_center">
                      <span>{{slotProps.item.name}}</span>
                      <b v-show="slotProps.item.switch">{{$t('homePage.turnON')}}</b>
                      <b v-show="!slotProps.item.switch">{{$t('homePage.turnOFF')}}</b>
                      <div class="wifi">
                        <img v-if="wifiStrength(slotProps.item)===1&&slotProps.item.state" src="../image/home/wifi_poor.png">
                        <img v-if="wifiStrength(slotProps.item)===2&&slotProps.item.state" src="../image/home/wifi_weak.png">
                        <img v-if="wifiStrength(slotProps.item)===3&&slotProps.item.state" src="../image/home/wifi_good.png">
                        <img v-if="wifiStrength(slotProps.item)===4&&slotProps.item.state" src="../image/home/wifi_full.png">
                        <strong v-if="slotProps.item.state&&slotProps.item!==undefined&&slotProps.item.wifiInfomation!==undefined">{{slotProps.item.wifiInfomation.ssid}}</strong>
                        <img src="../image/home/leave.png" v-if="!slotProps.item.state" alt="">
                        <strong v-if="!slotProps.item.state" class="redColor">{{$t('homePage.offline')}}</strong>
                      </div>
                    </div>
                    <div class="col_right">
                      <span @click.stop="socketSwitch(slotProps.item)">
                        <img v-show="slotProps.item.switch" src="../image/home/on.png">
                        <img v-show="!slotProps.item.switch" src="../image/home/off.png">
                      </span>
                    </div>
                  </div>
                </a>
              </div>
            </slider>
          </scroll>
        </div>

        <div class="no_device" v-show="!deviceList.length||!deviceList">
          <span>{{$t('homePage.noDevice')}}</span>
          <strong>{{$t('homePage.addIcon')}}</strong>
        </div>
      </div>

      <div class="discovery" v-show="foot_active===2">
       <div class="header">{{$t('homePage.store')}}</div>
       <section class="main-content">
        <ul class="dc-column">
          <div class="top-pixel"></div>
          <li @click="listNav(1)">
            <a>
              <div class="column-icon">
                <img src="../image/discovery/retail.png">
              </div>
              <div class="column-title">{{$t('homePage.shop')}}</div>
              <div class="column-describe"></div>
              <div class="column-after">
                <img src="../image/global/after.png">
              </div>
            </a>
            <div class="bom-pixel"></div>
          </li>
          <div class="bom-pixel"></div>
        </ul>

     <!--     <ul class="dc-column">
            <div class="top-pixel"></div>
            <li>
              <a>
                <div class="column-icon">
                  <img src="../image/discovery/virtual_experience.png">
                </div>
                <div class="column-title">虚拟体验</div>
                <div class="column-describe"></div>
                <div class="column-after">
                  <img src="../image/global/after.png">
                </div>
              </a>
            </li>
            <div class="bom-pixel"></div>
          </ul> -->
        </section>
      </div>

      <div class="user" v-show="foot_active===3">
        <section class="main-content">
          <div class="user-msg">
            <div class="wraper" @click="goPersonal">
              <div class="item">{{$t('homePage.me')}}</div>
              <div class="item">
                <span v-if="$store.state.LoginState" :style="{backgroundImage: 'url(' + userinfo.headPic + ')'}">
                </span>
                <span v-else class="pic">
                </span>
              </div>
              <div class="item">
                <span v-if="$store.state.LoginState&&$i18n.locale==='en'">{{userinfo.userName || userinfo.email}}</span>
                <span v-if="$store.state.LoginState&&$i18n.locale==='zh'">{{userinfo.userName || userinfo.mobile}}</span>
                <span v-if="!$store.state.LoginState">{{$t('userPage.notLoggedIn')}}</span>
              </div>
              <div class="item">
                <span v-show="$store.state.LoginState">{{$t('homePage.devicePre')}} {{deviceList.length}} {{$t('homePage.deviceSuf')}}</span>
              </div>
              <div class="item">
                <span>
                  {{$t('homePage.edit')}}
                  <img src="../image/user/Pen.png" alt="">
                </span>
              </div>
            </div>
          </div>
          <ul class="user-setup">
              <li @click="selectItem(1)" v-show="$i18n.locale==='en'">
                <a>
                  <div class="top-pixel"></div>
                  <a>
                    <div class="setup-text">
                      <strong><img src="../image/user/password.png" alt=""></strong>
                      <span>{{$t('homePage.changePassword')}}</span>
                    </div>
                    <div class="setup-after">
                      <img src="../image/global/after.png">
                    </div>
                  </a>
                  <div class="bom-pixel"></div>
                </a>
              </li>
              <li @click="selectItem(2)">
                <a>
                  <a>
                    <div class="setup-text">
                      <strong><img src="../image/user/opinion.png" alt=""></strong>
                      <span>{{$t('homePage.Language')}}</span>
                    </div>
                    <div class="setup-after">
                      <img src="../image/global/after.png">
                    </div>
                  </a>
                  <div class="bom-pixel"></div>
                </a>
              </li>
              <li @click="selectItem(3)">          
                <!-- /us/update -->
                <a> 
                  <a>
                    <div class="setup-text">
                      <strong><img src="../image/user/update.png"></strong>  
                      <span>{{$t('homePage.updates')}}</span> 
                    </div>
                    <div class="setup-after">
                      <img src="../image/global/after.png">
                    </div>
                  </a>
                  <div class="bom-pixel"></div>
                </a>
              </li>
              <li @click="selectItem(4)">          
                <a>
                  <a>
                    <div class="setup-text">
                      <strong><img src="../image/user/about.png" ></strong>  
                      <span>{{$t('homePage.AboutUs')}}</span> 
                    </div>
                    <div class="setup-after">
                      <img src="../image/global/after.png">
                    </div>
                  </a>
                </a>
              </li>
              <div class="bom-pixel"></div>
          </ul>
        </section>
      </div>
    </div>
    <footer class="footer">
      <div class="top-pixel"></div>
      <div class="foot-left">
        <a @click="chooseNav(1)">
          <div class="foot-col-icon">
            <img v-if="foot_active == 1" src="../image/home/Clipboard.png">
            <img v-if="foot_active != 1" src="../image/home/Clipboarddefault.png">
          </div>
          <div class="foot-col-text" :class="foot_active == 1 ? 'foot-active' : ''">{{$t('homePage.device')}}</div>
        </a>
      </div>
      <div class="foot-center">
        <a @click="chooseNav(2)">
          <div class="foot-col-icon">
            <img v-if="foot_active == 2" src="../image/home/OnlineSupermarketCartclick.png">
            <img v-if="foot_active != 2" src="../image/home/OnlineSupermarketCart.png">
          </div>
          <div class="foot-col-text" :class="foot_active == 2 ? 'foot-active' : ''">{{$t('homePage.store')}}</div>
        </a>
      </div>
      <div class="foot-right">
        <a @click="chooseNav(3)">
          <div class="foot-col-icon">
            <img v-if="foot_active == 3" src="../image/home/user_on.png">
            <img v-if="foot_active != 3" src="../image/home/user_off.png">
          </div>
          <div class="foot-col-text" :class="foot_active == 3 ? 'foot-active' : ''">{{$t('homePage.me')}}</div>
        </a>
      </div>
    </footer>

    <!-- 弹窗 -->
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='deleteEquipmentOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('messageBox.deleteDevice')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='versionUpdateOpreation'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span></span>
        </div>
        <div class="alert_describe">{{$t('userPage.discoverNewVersion')}}</div>
        <div class="alert_operation">
          <div class="cancle">
            <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
          </div>
          <div class="determine">
            <span @click="alertChoose(2)">{{$t('messageBox.confirm')}}</span>
          </div>
        </div>
      </div>
    </messageBox>
    <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='versionUpdateProgress'">
        <div slot="confirmColumn" class="common_alert updating">
          <div class="alert_icon">
            <span></span>
          </div>
          <div class="alert_describe container">
            <span>正在更新</span>
            <div class="progressBar">
              <span :style="{width: progressBar + '%'}"></span>
              <strong>{{progressBar}}%</strong>
            </div>
          </div>
          <div class="alert_operation">
            <div class="iknow determine">
              <span @click="alertChoose(3)" class="button">取消</span>
            </div>
          </div>
        </div>
      </messageBox>

    <!-- 操作表 -->
    <actionSheet setHeight="150" v-show="actionSheetType == 'homeSheet'">
      <ul class="home_sheet" slot="main">
        <li @click="sheetItem(1)">
          <a>{{$t('homePage.addDevicesEntry')}}</a>
          <div class="bom-pixel"></div>
        </li>
        <li @click="sheetItem(2)">
          <a>{{$t('homePage.findLAN')}}</a>
          <div class="bom-pixel"></div>
        </li>
        <li @click="sheetItem(0)">
          <a>{{$t('actionSheet.cancel')}}</a>
        </li>
      </ul>
    </actionSheet>

    <!-- 吐司 -->
     <toast :describe="$t('toast.setError')" v-if="ToastType==='setError'" position="bottom"></toast>
     <toast :describe="$t('userPage.latesetVersion')" v-if="ToastType==='latestVersion'" position="bottom"></toast>

    <transition :name="transitionName" mode="in-out">
      <router-view></router-view>
    </transition>

  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
import {
  isLoginRequest,
  deviceListRequest,
  wifiPowerSwitchRequest,
  unbundlingDeviceRequest,
  systemLanguageRequest,
  // disableGoBackRequest,
  inspectversionUpdateRequest,
  versionUpdateRequest,
  userInformationRequest,
  cancelVersionUpdateRequest,
  goToMallRequest,
  setStatusBarRequest
} from '../service/method'
import {
  isLoginResponse,
  deviceListResponse,
  logoutUserResponse,
  unbundlingDeviceResponse,
  wifiPowerSwitchResponse,
  setSystemLanguageResponse,
  systemLanguageResponse,
  temperatureAndHumidityDataResponse,
  networkStatusResponse,
  inspectVersionUpdateResponse,
  userInformationResponse,
  modifyUserInformationResponse,
  versionUpdateResponse
} from '../service/getData.js'
import { } from 'mint-ui'
import messageBox from '../components/common/messageBox'
import actionSheet from '../components/common/actionSheet'
import Slider from '../components/Slider'
import toast from '../components/common/toast'
import { clickInterval } from '../config/utils.js'

export default {
  data () {
    return {
      scroll: null, // 普通模式滚动
      foot_active: 1, // 底部导航
      currentItem: null, // 当前选择项
      delIcon: require('../image/home/del.png'),
      password: '', // 下拉
      topTip: 1, // 1、下拉刷新   2、释放立即刷新
      deviceList: [], // 设备列表
      pullRefreshObj: {
        pulldown: true, // 下拉刷新
        finishPullDown: true, // 下拉刷新完成
        pullup: false, // 上拉加载
        finishPullUp: true, // 上拉加载完成
        noMoreData: false // 没有更多数据
      },
      listenScroll: true, // 滚动监听
      progressBar: 0 // 更新进度条
    }
  },

  mounted () {
    // console.log(new Date().getSeconds()+'s '+new Date().getMilliseconds()+ 'ms')
    // 网络状态返回
    networkStatusResponse(this.NAVIGATOR, this)

    // 查询系统语言返回
    systemLanguageResponse(this.NAVIGATOR, this)

    // 获取设备列表返回
    deviceListResponse(this.NAVIGATOR, this, 'home')

    // 验证用户是否登录返回
    isLoginResponse(this.NAVIGATOR, this)

    // 插座电源开关状态返回
    wifiPowerSwitchResponse(this.NAVIGATOR, this, 'home')

    // 解绑设备返回
    unbundlingDeviceResponse(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 注销用户返回
    logoutUserResponse(this.NAVIGATOR, this)

    // 温湿度页面数据
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'home')

    // 检查是否有版本更新返回
    inspectVersionUpdateResponse(this.NAVIGATOR, this)

    // 查询用户信息返回
    userInformationResponse(this.NAVIGATOR, this)

    // 修改用户信息返回
    modifyUserInformationResponse(this.NAVIGATOR, this)

    // 版本更新返回
    versionUpdateResponse(this.NAVIGATOR, this)

    // 首次启动
    if (this.firstRunning) {
      this._initInterface()
      this.$store.state.firstRunning = false
    }

    if (this.$store.state.LoginState) {
      // 获取设备列表
      deviceListRequest(this.NAVIGATOR, this)

      // 查询用户信息
      userInformationRequest(this.NAVIGATOR, this)
    }

    if (this.$root.ISDEBUG) {
      console.log(this.currentRouter)
      // this.MESSAGEBOX_SWITCH({type: 'inputPasswordOpreation'})
      // 登录状态
      window.isLoginResponse(true)
      // console.log(this.$store.state.LoginState)
      if (this.$store.state.LoginState) {
        window.deviceListResponse('[{"name":"卧室空调的插座00","state":true,"switch":true,"mac":"00:00:00:00:00","wifiInfomation":{"ssid":"PHICOMM_startai","strength":0}},{"name":"卧室空调的插座01","state":true,"switch":true,"mac":"00:00:00:00:01","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-95}},{"name":"卧室空调的插座02","state":false,"switch":true,"mac":"00:00:00:00:02","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-70}},{"name":"卧室空调的插座03","state":false,"switch":true,"mac":"00:00:00:00:03","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-72}},{"name":"卧室空调的插座04","state":true,"switch":true,"mac":"00:00:00:00:04","wifiInfomation":{"ssid":"PHICOMM_startai","strength":-100}}]')
        console.log(this.deviceList)
      }

      // setTimeout(() => {
        // window.wifiPowerSwitchResponse('00:00:00:00:02', false)
        // 登出
        // window.logoutUserResponse(true)
        // 网络
        // window.networkStatusResponse('', false, '')
        // 解绑
        // window.unbundlingDeviceResponse(true, this.currentDevice.mac)
      // }, 3000)
    }

    // this.$nextTick(() => {
    //   this.scroll = new Bscroll(this.$refs.warpper, {
    //     click: true,
    //     bounceTime: 700
    //   })
    // })
  },

  components: {
    messageBox,
    actionSheet,
    Slider,
    toast
  },

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'actionSheet', 'actionSheetType', 'currentDevice', 'currentRouter', 'LoginState', 'ToastType', 'firstRunning'
    ]),

    ...mapGetters([
      'userinfo', 'responseType'
    ])
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH', 'TABLE_SWITCH', 'CLEAR_USERINFO'
    ]),

    ...mapActions([
      'setUserinfo', 'loadingAlert'
    ]),

    _initInterface () {
      // 查询系统语言
      systemLanguageRequest(this.NAVIGATOR, this)

      // 检验是否登录
      isLoginRequest(this.NAVIGATOR, this)
    },

    listNav (num) {
      // this.$router.push({path: '/home/shop'})
      // 禁用原生返回
      // disableGoBackRequest(this.NAVIGATOR, this, false)
      goToMallRequest(this.NAVIGATOR, this, 'https://www.amazon.com/s?marketplaceID=ATVPDKIKX0DER&me=A3KNQ5UR6RZMP9&merchant=A3KNQ5UR6RZMP9&redirect=true')
    },

    // 下拉刷新
    refreshData () {
      console.log('refreshData')
      // 获取设备列表
      deviceListRequest(this.NAVIGATOR, this)

      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          window.deviceListResponse('[{"name":"卧室空调的插座00","state":true,"switch":true,"mac":"00:00:00:00:00","wifiInfomation":{"ssid":"PHICOMM_startai","strength":0}}]')
        }, 3000)
      }
    },

    // 上拉加载
    loadData () {
      // console.log('loadData')
      // setTimeout(() => {
      //   console.log('finish')
      //   this.pullRefreshObj.finishPullUp = true
      //   this.pullRefreshObj.noMoreData = true
      // }, 10000)
    },

    // 初始化平移位置
    scrollHandle () {
      let listItems = document.querySelectorAll('.list-item')

      for (let i = 0; i < listItems.length; i++) {
        listItems[i].dataset.type = 0
      }
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          // 解绑设备返回
          unbundlingDeviceRequest(this.NAVIGATOR, this, this.currentItem.mac)
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 2:
          // 更新版本
          versionUpdateRequest(this.NAVIGATOR, this)
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType, bol: false})
          setTimeout(() => {
            this.MESSAGEBOX_SWITCH({type: 'versionUpdateProgress', bol: true})
            this.$store.state.disablemask = false
          }, 400)

          if (this.$root.ISDEBUG) {
            let i = 0
            let timer = setInterval(() => {
              if (i >= 100) {
                setTimeout(() => {
                  this.MESSAGEBOX_SWITCH({type: this.messageBoxType, bol: false})
                }, 1000)
                clearInterval(timer)
              }
              window.versionUpdateResponse(true, `{"url":"http://ip:port/xxx.apk","status":1,"extName":"mp3/mp4/apk","localPath":"sdcard/startai/download/","addedSize":100000,"totalSize":"","progress":${i++},"updateTime":"","fileName":"","fileId":"","protocol":"tcp http"}`)
            }, 100)

            // setTimeout(() => {
            //   window.versionUpdateResponse(true,'{"progress":100,"extName":"apk","protocol":"http","updateTime":1536312716272,"status":2,"localPath":"\/storage\/sdcard0\/startai\/download\/Zy1orFuPjmaAPJIsAngTdsWw238249.apk","fileName":"Zy1orFuPjmaAPJIsAngTdsWw238249.apk","totalSize":41423734,"url":"http:\/\/file.startai.com.cn:3108\/group1\/M00\/00\/0E\/Zy1orFuPjmaAPJIsAngTdsWw238249.apk","addedSize":41423734}')
            //   console.log(555556)
            // }, 1000)
          }
          break
        case 3:
          // 取消更新版本
          cancelVersionUpdateRequest(this.NAVIGATOR, this)
          this.$store.state.disablemask = true
          break
      }
    },

    sheetItem (num) {
      switch (num) {
        case 0:
          break
        case 1:
          this.$router.push({name: 'adddirver'})
          this.$store.state.findPage = false
          break
        case 2:
          this.$router.push({name: 'choose'})
          this.$store.state.findPage = true
          break
      }
      this.TABLE_SWITCH({type: this.actionSheetType})
    },

    add () {
      // console.log(this.$store.state.LoginState)
      if (this.$store.state.LoginState) {
        this.TABLE_SWITCH({type: 'homeSheet', flag: true})
      } else {
        this.$router.push({path: '/login'})
      }
    },

    goDevice (item) {
      console.log(item)
      this.$router.push({path: '/main'})
      this.$router.push({path: '/main/wifiState'})
      this.$store.state.jumpTimer = setTimeout(() => {
        this.$router.back()
        // 设置手机状态栏显示
        setStatusBarRequest(this.NAVIGATOR, this, true, '#4EA3FE')
      }, 5000)
      this.$store.state.currentDevice = item
    },

    deleteDevice (item) {
      console.log('deleteDevice')
      this.currentItem = item
      this.MESSAGEBOX_SWITCH({type: 'deleteEquipmentOpreation'})
    },

    // 个人中心
    goPersonal () {
      if (this.$store.state.LoginState) {
        // 查询用户信息
        // userInformationRequest(this.NAVIGATOR, this)

        this.$router.push('/home/personal')
      } else {
        this.$router.push('/login')
      }
    },

    selectItem (num) {
      switch (num) {
        case 1:
          if (this.$store.state.LoginState) {
            this.$router.push('/home/password')
          } else {
            this.$router.push('/login')
          }
          break
        case 2:
          this.$router.push('/home/language')
          break
        case 3:
          if (this.$store.state.LoginState) {
            // 检查版本更新
            inspectversionUpdateRequest(this.NAVIGATOR, this)
          } else {
            this.$router.push('/login')
          }

          if (this.$root.ISDEBUG) {
            window.inspectVersionUpdateResponse(true, '', true)
          }
          break
        case 4:
          this.$router.push('/home/about')
          break
      }
    },

    chooseNav (num) {
      this.foot_active = num
      switch (num) {
        case 1:
          break
        case 2:
          break
        case 3:
          break
      }
    },

    // WIFI信号强度
    wifiStrength (item) {
      // let array = [-25, -50, -75, -100]
      // if (item !== undefined && item.wifiInfomation !== undefined) {
      //   for (let i = 0; i < array.length; i++) {
      //     if (item.wifiInfomation.strength >= array[i] && item.wifiInfomation.strength <= 0) {
      //       return i + 1
      //     }
      //   }
      //   return 0
      // }
      if (item !== undefined && item.wifiInfomation !== undefined) {
        let MIN_RSSI = -100
        let MAX_RSSI = -55
        let rssi = item.wifiInfomation.strength
        // console.log(rssi)
        if (rssi <= MIN_RSSI) {
          return 1
        } else if (rssi >= MAX_RSSI) {
          return 4
        } else {
          let inputRange = MAX_RSSI - MIN_RSSI
          let strength = Math.floor((rssi - MIN_RSSI) * 4 / inputRange)
          if (strength === 0) strength = 1
          return strength
        }
      }
    },

    // 插座开关切换
    socketSwitch (item) {
      clickInterval(() => {
        // 插座电源开关控制
        wifiPowerSwitchRequest(this.NAVIGATOR, this, item.mac, !item.switch)

        if (this.$root.ISDEBUG) {
          item.switch = !item.switch
          // console.log(1111)
          // window.wifiPowerSwitchResponse('00:00:00:00:00', !item.switch)
        }
      }, false, 2000)
    }
  },

  watch: {
    currentRouter: function (newValue) {
      if (newValue === '/home') {
        // 绑定设备后跳转主页请求数据
        if (this.$route.params.binding) {
          // 获取设备列表
          deviceListRequest(this.NAVIGATOR, this)
        }
      }
    }
  }
}
</script>

