const user = {
  state: {
    token: '',
    userinfo: {
      headPic: require('../../image/user/head.png')
    },
    responseType: '' // 修改信息返回类型
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_USERINFO: (state, msg) => {
      state.responseType = ''
      state.userinfo = Object.assign(state.userinfo, msg)
      console.log(state.userinfo)
    },
    CLEAR_USERINFO: (state) => {
      state.userinfo = {
        headPic: require('../../image/user/head.png')
      }
    },
    SET_RESPONSE_TYPE: (state, type) => {
      state.responseType = type
    }
  },
  actions: {
    setUserinfo ({commit}, msg) {
      commit('SET_USERINFO', msg)
    },
    setResponseType ({commit}, type) {
      commit('SET_RESPONSE_TYPE', type)
    }
  }
}

export default user



